export class Plan {

    client;
    subjects: any = [[], [], [], [], []]; // One subject list for each day (0-Monday .. 4-Friday)
    template: string;
    daySelected: number;

    constructor() {
    }

    init(): any {
        this.daySelected = 0;

        this.subjects[0].push({
            name: "Geschichte",
            slot: "1",
            teacher: "Pfanni",
            status: "Regular"
        });

        this.subjects[0].push({
            name: "Deutsch",
            slot: "2",
            teacher: "German Dude",
            status: "Regular"
        });


    }


    reloadTemplate(day: number): Promise<string> {
        let template = require('./templates/substitutionPlan.hbs');
        let rendered = template(
            {
                data: this.subjects[day]
            });
        return Promise.resolve(rendered)
    }

    addSubject(subj: Subject): void {
        this.subjects[subj.day].push({
            name: subj.name,
            slot: subj.slot,
            teacher: subj.teacher,
            status: this.setStatus(subj.status)
        });

        this.reloadTemplate(0)
    }

    changeSubject(): void {

    }

    setStatus(status: number): string {
        switch (status) {
            case 0:
                return "Regular";
            case 1:
                return "Substitution";
            case 2:
                return "Canceled";
            default:
                return ""

        }
    }
}

class Subject {

    name: string;
    slot: number;
    day: number;
    teacher: string;
    status: number;


    constructor(name: string, slot: number, day: number, teacher: string, status: number) {
        this.name = name;
        this.slot = slot;
        this.day = day;
        this.teacher = teacher;
        // status: 0 = regular, 1 = substitution, 2 = canceled
        this.status = status;
    }

    setSubstitutionTeacher(teacher: string) {
        this.teacher = teacher;
        this.status = 1;
    }

    cancelSubject() {
        this.name = this.name.strike();
        this.teacher = this.teacher.strike();
        this.status = 2
    }
}
