import {FrontendHandler} from "./FrontendHandler";
import {Plan} from "./plan";
import {PresentationCommunicator} from "./PresentationCommunicator";

// Do the mqtt stuff
let pc = new PresentationCommunicator();
pc.init();
let client = pc.client;

// Do the frontend stuff
let fh = new FrontendHandler(client);
if (document.readyState != 'loading') {
    fh.init();
} else {
    console.log('Document was not ready, waiting for it to load');
    document.addEventListener('DOMContentLoaded', function () {
        fh.init();
    });
}

